import React, { useState, useEffect, useContext, useRef } from 'react';
import { LoginContext } from './Authenticate';
import { SocketContext } from './WithSocket';
import { Paint } from './Paint';
import '../css/TextChat.css';

export const TextChat = () => {
  const user = useContext(LoginContext);
  const username = user ? user.username : '';
  const socketRef = useContext(SocketContext);
  const socket = socketRef ? socketRef.current : null;
  const [inputMessage, setInputMessage] = useState('');
  const [messages, setMessages] = useState([]);
  const lastLineRef = useRef(null);
  const [chatTo, setChatTo] = useState('*');
  const [userList, setUserList] = useState([]);
  const paintRef = useRef(null);
  const [hoveredMessage, setHoveredMessage] = useState(null);

  const handleTextMessage = (data) => {
    setMessages((prevMessages) => [...prevMessages, data]);
  };

  const handleUserList = (data) => {
    setUserList(data);
    if (data.indexOf(chatTo) < 0) {
      setChatTo('*');
    }
  };

  const handleReaction = (data) => {
    setMessages((prevMessages) =>
      prevMessages.map((msg) =>
        msg.time === data.time && msg.from === data.from
          ? { ...msg, reactions: [...(msg.reactions || []), data.reaction] }
          : msg
      )
    );
  };

  const sendTextMessage = () => {
    if (socket) {
      socket.emit('text', {
        from: username,
        to: chatTo,
        text: inputMessage,
        image: paintRef.current.getImageDataURL(),
        time: Date.now(),
      });
      setInputMessage('');
    }
  };

  const sendReaction = (message, reaction) => {
    if (socket) {
      socket.emit('reaction', {
        from: message.from,
        time: message.time,
        reaction,
      });
    }
  };

  const inputChanged = (event) => {
    setInputMessage(event.target.value);
  };

  const handleKeyUp = (event) => {
    if (event.key === 'Enter' && inputMessage.length > 0) {
      sendTextMessage();
    }
  };

  const selectChatTo = (event) => {
    setChatTo(event.target.value);
  };

  useEffect(() => {
    if (socket) {
      console.log('[TextChat] adding listeners');
      socket.on('text', handleTextMessage);
      socket.on('user-list', handleUserList);
      socket.on('reaction', handleReaction);
      socket.emit('user-list', { from: username });
      return () => {
        console.log('[TextChat] removing listeners');
        socket.off('text', handleTextMessage);
        socket.off('user-list', handleUserList);
        socket.off('reaction', handleReaction);
      };
    }
  }, [socket, username, chatTo]);

  useEffect(() => {
    if (lastLineRef.current) {
      lastLineRef.current.scrollIntoView({ block: 'nearest' });
    }
  }, [messages]);

  return (
    <div className="text-chat">
      <div className="text-chat-message-list-container">
        <div className="text-chat-message-list">
          {messages.map((message) => (
            <div
              key={message.from + message.time}
              className={
                message.from === username
                  ? 'text-chat-from-me text-chat-message'
                  : 'text-chat-from-them text-chat-message'
              }
              onMouseEnter={() => setHoveredMessage(message)}
              onMouseLeave={() => setHoveredMessage(null)}
            >
              <div className="message-header">
                {message.from !== username && <span>{message.from} &gt; </span>}
                <span>{message.text}</span>
              </div>
              {message.image && (
                <img
                  src={message.image}
                  alt="paint"
                  onClick={(event) =>
                    paintRef.current.putImageDataURL(event.target.src)
                  }
                />
              )}
              <div className="reactions">
                {message.reactions &&
                  message.reactions.map((reaction, index) => (
                    <span key={index} className="reaction-mark">
                      {reaction}
                    </span>
                  ))}
              </div>
              {hoveredMessage === message && (
                <div className="reaction-buttons">
                  <button onClick={() => sendReaction(message, 'üëç')}>üëç</button>
                  <button onClick={() => sendReaction(message, '‚ù§Ô∏è')}>‚ù§Ô∏è</button>
                  <button onClick={() => sendReaction(message, 'üòÇ')}>üòÇ</button>
                </div>
              )}
            </div>
          ))}
          <div ref={lastLineRef}></div>
        </div>
      </div>
      <div className="text-chat-input">
        <select onChange={selectChatTo} value={chatTo}>
          <option value="*">*</option>
          {userList.map((u) => (
            <option key={u} value={u}>
              {u}
            </option>
          ))}
        </select>
        <input
          type="text"
          onChange={inputChanged}
          value={inputMessage}
          onKeyUp={handleKeyUp}
        />
        <button
          type="button"
          onClick={sendTextMessage}
          disabled={socket === null || inputMessage === ''}
        >
          ÈÄÅ‰ø°
        </button>
      </div>
      <Paint ref={paintRef} />
    </div>
  );
};



