/**
 * Á∞°Êòì„É≠„Ç∞„Ç§„É≥Ê©üËÉΩ
 * <Authenticate> ... </Authenticate>
 * 
 * Êú™„É≠„Ç∞„Ç§„É≥ÊôÇ: „É≠„Ç∞„Ç§„É≥„ÅÆÁîªÈù¢„ÇíË°®Á§∫„Åô„ÇãÔºé
 * „É≠„Ç∞„Ç§„É≥ÊôÇÔºöÂ≠êË¶ÅÁ¥†„ÇíË°®Á§∫„Åô„ÇãÔºé„Å™„Çâ„Å≥„Å´„É≠„Ç∞„Ç™„Éï„Éú„Çø„É≥„Çí‰∏äÈÉ®„Å´Ë°®Á§∫„Åô„ÇãÔºé
 * 
 * [Ëá™Âãï„É≠„Ç∞„Ç§„É≥„ÅÆÂÆüÈ®ìÁöÑÊ©üËÉΩ]
 * props„Å®„Åó„Å¶Ôºåusername, password„ÅåÊ∏°„Åï„Çå„Çã„Å®Ôºå„Ç≥„É≥„Éù„Éº„Éç„É≥„Éà„ÅÆ„Éû„Ç¶„É≥„ÉàÊôÇ„Å´
 * ÊåáÂÆö„Åï„Çå„Åüusername„Å®password„Çí‰Ωø„Å£„Å¶Ëá™ÂãïÁöÑ„Å´„É≠„Ç∞„Ç§„É≥„ÇíË©¶„Åø„Çã„Åì„Å®„Å´„Åô„ÇãÔºé
 * „É≠„Ç∞„Ç¢„Ç¶„Éà„Éú„Çø„É≥„ÅØË°®Á§∫„Åï„Çå„Å™„ÅÑÔºé
 * [ÈñãÁô∫ÊîØÊè¥Áî®ÔºölocalStorage„Å´„É¶„Éº„Ç∂Âêç„Çí‰øùÂ≠ò„Åô„ÇãÊ©üËÉΩ]
 * props„Å´ usernameSaveKey „ÅåÊåáÂÆö„Åï„Çå„Çã„Å®Ôºå
 * usernameSaveKey„ÅßÊåáÂÆö„Åï„Çå„ÅüÂÄ§„Çíkey„Å®„Åó„Å¶localStorage„Å´„É¶„Éº„Ç∂Âêç„Çí‰øùÂ≠ò„Åô„ÇãÔºé
 * ‰øùÂ≠ò„Åï„Çå„Åü„É¶„Éº„Ç∂Âêç„ÅØÔºåÊ¨°Âõû„ÅÆ„Éû„Ç¶„É≥„ÉàÊôÇ„Å´Ëá™ÂãïÁöÑ„Å´Ë™≠„ÅøËæº„Åæ„ÇåÔºå„É¶„Éº„Ç∂Âêç„Å´Ë®≠ÂÆö„Åï„Çå„ÇãÔºé
 * „Åù„ÅÆÈöõ„Å´„ÅØ‰æøÂÆúÁöÑ„Å´password„ÅØusername„Å®Âêå„Åò„ÇÇ„ÅÆ„Å´Ë®≠ÂÆö„Åï„Çå„ÇãÔºé
 * „É¶„Éº„Ç∂„ÅØ„É≠„Ç∞„Ç§„É≥„Éú„Çø„É≥„Çí„ÇØ„É™„ÉÉ„ÇØ„Åô„Çå„Å∞„É≠„Ç∞„Ç§„É≥„Åï„Çå„Çã„Åì„Å®„Å´„Å™„ÇãÔºé
 */
import '../css/Authenticate.css';

import { useState, useRef, createContext, useEffect } from 'react';

// „É¶„Éº„Ç∂ÊÉÖÂ†±„ÇíÂ≠êË¶ÅÁ¥†„Å´Ê∏°„Åô„Åü„ÇÅ„Å´ context „Çí‰ΩøÁî®„Åô„ÇãÔºé
export const LoginContext = createContext();

export const Authenticate = (props) => {
  // localStorage„Å´‰øùÂ≠ò„Åô„Çã„É¶„Éº„Ç∂Âêç„ÅÆ„Ç≠„Éº
  const usernameKey = props.usernameSaveKey ?
    `${document.location.pathname}#Authenticate${props.usernameSaveKey}` : undefined;
  // localStorage„Åã„Çâ„É¶„Éº„Ç∂Âêç„ÇíÂèñÂæó„Åô„ÇãÔºéÁÑ°„Åë„Çå„Å∞ÂÄ§„Çínull„Å´„Åô„ÇãÔºé
  const savedUsername = usernameKey ? localStorage.getItem(usernameKey) : null;

  // „É¶„Éº„Ç∂Âêç„ÅÆÂÖ•Âäõ
  const [usernameInput, setUsernameInput] = useState(props.username !== undefined ?
    props.username : savedUsername);

  // password„ÅÆÂÖ•Âäõ useRef„Çí‰Ωø„Å£„Å¶ÔºåÂÄ§„ÇíÂèñÂæó„Åô„ÇãÔºé
  const passwordInputRef = useRef('');

  // /login„Å´POST„É™„ÇØ„Ç®„Çπ„Éà„ÇíÈÄÅ„Å£„ÅüÈöõ„ÅÆ„Ç®„É©„Éº„É°„ÉÉ„Çª„Éº„Ç∏„ÇíË°®Á§∫„Åô„ÇãÔºé
  const [errorMessage, setErrorMessage] = useState('');

  // „É¶„Éº„Ç∂ÊÉÖÂ†±Ôºàusername„Å®tokenÔºâ„Çí‰øùÊåÅ„Åô„ÇãÔºé
  const [user, setUser] = useState(null);

  // „É≠„Ç∞„Ç§„É≥Ôºölogin„ÅÆAPI„ÇíÂëº„Å∂Ôºé
  const login = async () => {
    try {
      setErrorMessage('');
      const response = await fetch('/login', {
        method: 'post',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          // username „Å® password „ÇíÊ∏°„ÅôÔºé
          username: usernameInput,
          password: passwordInputRef.current.value
        })
      });
      if (!response.ok) {
        // status„Åå200Áï™Âè∞‰ª•Â§ñ„ÅÆÊôÇ„ÅØ„Ç®„É©„Éº„Å®„Åó„Å¶Êâ±„ÅÜÔºé
        if (response.status === 403) {
          // Forbidden„Ç®„É©„Éº„Åß„ÅÇ„Çå„Å∞Ôºå„É≠„Ç∞„Ç§„É≥„Ç®„É©„Éº„Å®„Åó„Å¶Êâ±„ÅÜÔºé
          throw new Error('„É≠„Ç∞„Ç§„É≥„Éª„Ç®„É©„Éº');
        } else {
          // „Åù„Çå‰ª•Â§ñ„Åß„ÅÇ„Çå„Å∞Ôºåstatus„Ç≥„Éº„Éâ„Å®status„ÉÜ„Ç≠„Çπ„Éà„Çí„Ç®„É©„Éº„Å®„Åó„Å¶Ë°®Á§∫„Åô„Çã„Åì„Å®„Å´„Åô„ÇãÔºé
          throw new Error(`${response.status} ${response.statusText}`);
        }
      }
      // „É¶„Éº„Ç∂ÊÉÖÂ†±Ôºàusername„Å®tokenÔºâ„ÅåJSON„É¨„Çπ„Éù„É≥„Çπ„Å®„Åó„Å¶Ëøî„Å£„Å¶„Åè„Çã„ÅÆ„ÅßÔºå„Åì„Çå„Çíuser„Å´‰øùÂ≠ò„Åô„ÇãÔºé
      const data = await response.json();
      setUser(data);
      // „É¶„Éº„Ç∂Âêç„ÇílocalStorage„Å´‰øùÂ≠ò„Åô„ÇãÔºé
      if (usernameKey !== undefined) {
        localStorage.setItem(usernameKey, usernameInput);
      }
    } catch (error) {
      setErrorMessage(error.message);
      // „Ç®„É©„Éº„ÅÆÂ†¥Âêà„ÅØÔºå„É¶„Éº„Ç∂ÊÉÖÂ†±„ÇíÂâäÈô§„Åô„Çã„Åì„Å®„Å´„Åô„ÇãÔºé
      setUser(null);
      if (usernameKey !== undefined) {
        localStorage.removeItem(usernameKey);
      }
    }
  };

  // „É≠„Ç∞„Ç¢„Ç¶„ÉàÔºö„É¶„Éº„Ç∂ÊÉÖÂ†±„ÇíÂâäÈô§„Åô„ÇãÔºé
  const logout = () => {
    setUser(null);
    if (usernameKey !== undefined) {
      localStorage.removeItem(usernameKey);
    }
  };

  // ÂÆüÈ®ìÁöÑ„Å™Ëá™Âãï„É≠„Ç∞„Ç§„É≥Ê©üËÉΩ
  useEffect(() => {
    // username„Å®password„Ååprops„Å´ÊåáÂÆö„Åï„Çå„Å¶„ÅÑ„ÇãÊôÇ„Å´„ÅØËá™Âãï„É≠„Ç∞„Ç§„É≥„ÇíË°å„ÅÜ„Åì„Å®„Å´„Åô„ÇãÔºé
    if (props.username !== undefined && props.password !== undefined) {
      // password„ÇíË®≠ÂÆö„Åô„ÇãÔºé
      passwordInputRef.current.value = props.password;
      // „É≠„Ç∞„Ç§„É≥„ÇíË©¶„Åø„ÇãÔºé
      login();
    } else if (usernameKey !== undefined) {
      // „É¶„Éº„Ç∂Âêç„ÇílocalStorage„Åã„ÇâÂèñÂæó„Åô„ÇãÔºé
      const username = localStorage.getItem(usernameKey);
      if (username !== null) {
        // ‰æøÂÆúÁöÑ„Å´password„Çíusername„Å®Âêå„Åò„ÇÇ„ÅÆ„Å´Ë®≠ÂÆö„Åô„ÇãÔºé
        passwordInputRef.current.value = username;
        // „É¶„Éº„Ç∂Âêç„ÇíË®≠ÂÆö„Åô„ÇãÔºé
        setUsernameInput(username);
        // Ëá™ÂãïÁöÑ„Å´„É≠„Ç∞„Ç§„É≥„Åô„ÇãÂ†¥Âêà„ÅØÔºålogin„ÇíÂëº„Å∂Ôºé
        // login();
      }
    }
  }, []);

  return (user ?
    <div>
      {/* „É≠„Ç∞„Ç§„É≥„Åó„Å¶„ÅÑ„ÇãÂ†¥Âêà */}
      <div className="authenticate-logout">
        <span>User: {user ? user.username : ''}</span>
        {/* Ëá™Âãï„É≠„Ç∞„Ç§„É≥„ÅÆÂ†¥Âêà„ÅØ„É≠„Ç∞„Ç¢„Ç¶„Éà„Éú„Çø„É≥„ÇíË°®Á§∫„Åó„Å™„ÅÑ„Åì„Å®„Å´„Åô„ÇãÔºé */}
        {(props.username !== undefined && props.password !== undefined) ? null :
          <button type="button" onClick={logout}>„É≠„Ç∞„Ç¢„Ç¶„Éà</button>}
      </div>
      <LoginContext.Provider value={user}>
        {/* user„ÇíLoginContext„Å´Ê∏°„Åô„Å®„Å®„ÇÇ„Å´ÔºåÂ≠êË¶ÅÁ¥†„ÇíË°®Á§∫„Åô„ÇãÔºé */}
        {props.children}
      </LoginContext.Provider>
    </div>
    :
    <div>
      {/* Êú™„É≠„Ç∞„Ç§„É≥„ÅÆÂ†¥Âêà */}
      <div className="authenticate-login">
        <label>
          <span>„É¶„Éº„Ç∂Âêç:</span>
          <span><input type="text" placeholder="„É¶„Éº„Ç∂Âêç"
            onChange={(event) => setUsernameInput(event.target.value)} value={usernameInput} />
          </span>
        </label>
        <label>
          <span>„Éë„Çπ„ÉØ„Éº„Éâ:</span>
          <span><input type="password" ref={passwordInputRef}
            placeholder="„Éë„Çπ„ÉØ„Éº„Éâ" defaultValue='' />
          </span>
        </label>
        <button type="button" onClick={login}>„É≠„Ç∞„Ç§„É≥</button>
      </div>
      <div>
        {errorMessage === '' ? null :
          <div className="error-message" onClick={() => setErrorMessage('')}>
            {errorMessage}</div>}
      </div>
    </div>
  );
};

